import React, { Component } from 'react';
import './App.css';

var Highcharts = require('highcharts');
require('highcharts/modules/exporting')(Highcharts);

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {comments: []};
  }

  updateComments(comment) {
    this.setState((prevState) => {
      comments: prevState.comments.push(comment)
    }, () => {
      this.setState({comments: this.state.comments});
    });
  }

  render() {
    return (
      <div className="App">
        <ControlledForm 
          updateComments={this.updateComments.bind(this)}/>
        <Graph comments={this.state.comments} />
        <LatestComments comments={this.state.comments} />
      </div>
    );
  }
}

class ControlledForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      name: '',
      email: '',
      rating: '',
      comment: ''
    };

    this.handleChangeName = this.handleChangeName.bind(this);
    this.handleChangeEmail = this.handleChangeEmail.bind(this);
    this.handleChangeRating = this.handleChangeRating.bind(this);
    this.handleChangeComment = this.handleChangeComment.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleChangeName(event) {
    this.setState({name: event.target.value});
  }

  handleChangeEmail(event) {
    this.setState({email: event.target.value});
  }

  handleChangeRating(data) {
    this.setState({rating: data});
  }

  handleChangeComment(event) {
    this.setState({comment: event.target.value});
  }

  handleSubmit(event) {
    event.preventDefault();
    
    const comment = {
      name: this.state.name,
      email: this.state.email,
      rating: this.state.rating,
      comment: this.state.comment
    };

    this.props.updateComments(comment);
    this.setState({
      name: '',
      email: '',
      rating: '',
      comment: ''
    });
    this.resetRating();
  }

  resetRating() {
    var x = document.getElementsByClassName('fa-star');
    var i;
    for (i = 0; i < 5; i++) {
        x[i].className = "fa fa-star";
    }
  }

  handleRating(data) {
    this.resetRating();
    var x = document.getElementsByClassName('fa-star');
    var i;
    for (i = 0; i < data; i++) {
        x[i].className = "fa fa-star checked";
    }
    this.handleChangeRating(data);
  }

  render() {
    return (
      <div>
        <form onSubmit={this.handleSubmit} className="Form">
          <label>
            <input ref="nameInput" type="text" placeholder="Full Name" value={this.state.name} onChange={this.handleChangeName} />
          </label>
          <label>
            <input type="text" placeholder="Email Address" value={this.state.email} onChange={this.handleChangeEmail} />
          </label>
          <label>
            {/*<input type="text" placeholder="Rating eg: 3" value={this.state.rating} onChange={this.handleChangeRating} />*/}
            <span className="fa fa-star" onClick={() => this.handleRating(1)} />
            <span className="fa fa-star" onClick={() => this.handleRating(2)} />
            <span className="fa fa-star" onClick={() => this.handleRating(3)} />
            <span className="fa fa-star" onClick={() => this.handleRating(4)} />
            <span className="fa fa-star" onClick={() => this.handleRating(5)} />
          </label>
          <label>
            <textarea placeholder="Write a comment" value={this.state.comment} onChange={this.handleChangeComment} />
          </label>
          <input type="submit" value="Submit" />
        </form>
      </div>
    );
  }
}

class LatestComments extends React.Component {
  render() {
    return (
      <div className="latest-comments-box">
        <h2>Latest comments:</h2>
        { this.props.comments.map((data, i) => {
          return <CommentRow key={i} user={data} />
        })}
      </div>
    );
  }
}

class CommentRow extends React.Component {
  render() {
    return (
      <div className="comment-row">
        <p className="comment-text">{this.props.user.name}</p>
        <p className="comment-text">Rated {this.props.user.rating} stars.</p>
        <p className="comment-text">Wrote: {this.props.user.comment}</p>
      </div>
    );
  }
}

class Graph extends React.Component {
  constructor(props) {
    super(props);
    this.graphOptions = {
      chart: {
        renderTo: 'graph-container',
        type: 'bar'
      },
      title: {
        text: 'Statistics'
      },
      xAxis: {
        categories: ['Comments', 'Rating']
      },
      yAxis: {
        title: {
          text: 'Number of'
        }
      },
      series: []
    }
  }

  componentDidMount() {
    Highcharts.chart('graph-container', this.graphOptions);
  }

  componentWillReceiveProps(nextProps) {
    this.updateHighChart(nextProps.comments);
  }

  updateHighChart(data) {
    new Highcharts.Chart({
      chart: {
        renderTo: 'graph-container',
        type: 'bar'
      },
      title: {
        text: 'Statistics'
      },
      xAxis: {
        categories: ['Comments']
      },
      yAxis: {
        title: {
          text: 'Number of'
        }
      },
      series: [{
        data: [data.length]
      }]
    });
  }

  render() {
    return (
      <div id="graph-container" className="app-graph" />
    );
  }
}


export default App;



